@import url("https://fonts.googleapis.com/css2?family=Playfair+Display:wght@700&family=Poppins:wght@300;400&display=swap");
*{
    margin:0;
    padding: 0;
    box-sizing: border-box;
}
/* font-family:'Poppins',sans-sarif; */
/* font-family:'Playfair Display',serif; */
.navbar{
font-family:'Poppins',sans-serif;
font-size: 17px;
line-height: 26px;
text-decoration: uppercase;
box-shadow: 0 8px 6px -6px grey;
border-bottom: 0.1px solid grey !important;

}
.nav-link{
    font-weight: 300 !important;
}
.active{
    border-bottom: 0.5px solid black;
}
.navbar-brand{
  /* font-size:18px; */
  font-weight:600;
  font-family: "sans-serif";
  letter-spacing: 2px;
}
.footer{
    color:whitesmoke;
    padding:5px;
    background-color: black;
}
.search-btn{
    background: #000;
    color:white;
    border-radius: 0%;
}
.search-btn:hover{
    background-color: #0b0902;
}
.footer a{
    text-decoration: none;
    color:whitesmoke;
    padding:10px;
}
.footer a:hover{
    border-bottom:1px solid #ffefba ;
}

/* page not found */
.pnf{
    display: flex;
    min-height:65vh;
    flex-direction: column;
    align-items: center;
    justify-content:center;
}
.pnf-title{
    font-size: 50px;
    font-weight: 700;
}
.pnf-heading{
    font-weight:normal;
    font-size: 40px;
}
.pnf-btn{
    color:black;
    border: 1px solid black; 
    text-decoration: none;
    padding:10px;
    margin-top:10px;

}

