.home-page button {
  border-radius: 0;
  border-top-left-radius: 5px;
  border-bottom-right-radius: 5px;
  font-size: 14px;
  font-weight: bold;
}
.home-page button:hover{
cursor: pointer;
}
.home-page .card-text {
  color: rgb(90, 89, 89);
}

.home-page .card img:hover {
  transform: scale(0.9);
  
}
.card{
  background-color:whitesmoke;
}
